import Paper from "@material-ui/core/Paper";
import React, { useState, useEffect, useRef } from "react";
import { API } from "../constants/API";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
  },
}));

function Meme() {
  const classes = useStyles();
  const [memeImage, setMemeImage] = useState(null);
  const imagen = useRef();
  const [imageDim, setImageDim] = useState(null);

  useEffect(() => {
    fetch(API.memes)
      .then((data) => data.json())
      .then((data) => {
        setMemeImage(data[0].imgUrl);
      });
  }, []);

  useEffect(() => {
    setImageDim({
      width: imagen?.current?.naturalWidth,
      height: imagen?.current?.naturalHeight,
    });
  }, [memeImage]);

  return (
    <Paper className={[classes.root, "memeCont"]}>
      <p>Dato meme, por Daniel Sanchez</p>
      <div>
        {imageDim && (
          <img
            style={{
              width: imageDim.width > imageDim.height ? "100%" : "auto",
              height: imageDim.width < imageDim.height ? "100%" : "auto",
            }}
            ref={imagen}
            src={memeImage}
            alt="Dato Meme"
          />
        )}
      </div>
    </Paper>
  );
}

export default Meme;
