import { makeStyles } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import { Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { toggleMail, toggleLoadingReq } from "../features/counter/generalSlice";
import React, { useState } from "react";
import { API } from "../constants/API";
import getGeneralTab from "../features/counter/selector";

const useStyles = makeStyles((theme) => ({
  textField: {
    width: "90%",
    marginTop: "0.7rem",
    backgroundColor: "rgba(250, 250, 250, 0.2)",
  },
}));

function FormularioMail() {
  const { isLoadingReq } = useSelector(getGeneralTab());
  const classes = useStyles();
  const dispatch = useDispatch();
  const [name, setName] = useState("");
  const [location, setLocation] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const sendHandler = () => {
    dispatch(toggleLoadingReq());
    const bodyCont = {
      nombre: name,
      barrio: location,
      email,
      contenido: message,
    };
    fetch(API.contact, {
      method: "POST",
      body: JSON.stringify(bodyCont),
      headers: {
        connection: "keep-alive",
        "user-agent":
          "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/84.0.4147.105 Safari/537.36",
        accept: "*/*",
        "sec-fetch-site": "same-origin",
        "sec-fetch-mode": "cors",
        "sec-fetch-dest": "empty",
        referer: "https://datonews-api.herokuapp.com/mensajes/enviar",
        "accept-language": "es-US,es-419;q=0.9,es;q=0.8,en;q=0.7",
        "content-type": "application/json",
        host: "https://datonews-api.herokuapp.com",
        "accept-encoding": "gzip, deflate, br",
        "content-length": "84",
      },
    })
      .then((res) => res.text())
      .then((res) => {
        if (res === "Mensaje enviado correctamente") {
          setName("");
          setLocation("");
          setEmail("");
          setMessage("");
          dispatch(toggleLoadingReq());
          dispatch(toggleMail());
        }
      });
  };

  const handleNameChange = (event) => {
    setName(event.target.value);
  };

  const handleLocationChange = (event) => {
    setLocation(event.target.value);
  };

  const handleEmailChange = (event) => {
    setEmail(event.target.value);
  };

  const handleMessageChange = (event) => {
    setMessage(event.target.value);
  };

  const cancelHandler = () => {
    dispatch(toggleMail());
  };

  return (
    <form className="formCont" noValidate autoComplete="off">
      <TextField
        className={classes.textField}
        color="secondary"
        id="outlined-basic"
        label="Nombre*"
        variant="outlined"
        value={name}
        onChange={handleNameChange}
      />
      <TextField
        className={classes.textField}
        color="secondary"
        id="outlined-basic"
        label="Barrio/Localidad*"
        variant="outlined"
        value={location}
        onChange={handleLocationChange}
      />
      <TextField
        className={classes.textField}
        color="secondary"
        id="outlined-basic"
        label="E-mail*"
        variant="outlined"
        value={email}
        onChange={handleEmailChange}
      />
      <TextField
        className={classes.textField}
        color="secondary"
        id="outlined-basic"
        label="Tu mensaje"
        variant="outlined"
        multiline
        rows={4}
        value={message}
        onChange={handleMessageChange}
      />
      <div className="buttonFormCont">
        <Button
          disabled={isLoadingReq}
          onClick={cancelHandler}
          variant="contained"
          color="secondary"
        >
          Cancelar
        </Button>
        <Button
          disabled={isLoadingReq}
          onClick={sendHandler}
          variant="contained"
          color="secondary"
        >
          Enviar
        </Button>
      </div>
    </form>
  );
}

export default FormularioMail;
