import OliviaImg from "../../assets/Banners/OLIVIA_logo.jpg";

function Olivia() {
  return (
    <div class="bloque-pub-oliv" id="Olivia">
      <img
        title="Logo Olivia"
        class="olivia-img"
        src={OliviaImg}
        alt="Olivia Banner"
      />
      <div class="bloque-pub-texto">
        <p class="pub-titulo">Editorial Olivia</p>
        <p>
          EDITORIAL OLIVIA es un espacio de arte, dedicado al Management
          Editorial, la comunicación, exposición y difusión de obras para
          autores y artistas independientes. Somos un grupo de profesionales de
          la comunicación, autores e ilustradores independientes, amantes de los
          libros y del arte, dispuestos a acompañarte en el proceso de dar a
          conocer tu obra.
        </p>
        <p>Instagram @editorialolivia</p>
        <p>Escribinos: Info@editorialolivia.com</p>
      </div>
    </div>
  );
}

export default Olivia;
